import Vue from 'vue'
import Router from 'vue-router'
import LoginList from '@/components/LoginList'
import Home from '@/components/Home'
import MenuList2 from '@/components/MenuList2'
import MyOrder from '@/components/MyOrder'
import MenuDetail from '@/components/MenuDetail'
import Register from '@/components/Register'
import Message from '@/components/Message'
import ReceiveList from '@/components/ReceiveList'
import MenuMangeList from '@/components/MenuMangeList'
import MyCurOrder from '@/components/MyCurOrder'
import OrdersManage from '@/components/OrdersManage'
import MoneyManage from '@/components/MoneyManage'
import CateMana from '@/components/CateMana'
import Voucher from '@/components/Voucher'
import PostBlog from '@/components/PostBlog'
import BlogList from '@/components/BlogList'
import BlogDetail from '@/components/BlogDetail'
import DateSign from '@/components/DateSign'
import Example from '@/components/Example'
import Map from '@/components/Map'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/ms',
    name: '',
    hidden: true,
    component: Message
  },
  {
    path: '/',
    name: '',
    hidden: true,
    component: LoginList
  }, {
    path: '/re',
    component: Register,
    name: '',
    hidden: true
  }, {
    path: '/home',
    name: '',
    component: Home,
    hidden: true
  },

  {
    path: '/home',
    component: Home,
    name: '',

    children: [{
      
      path: '/MenuList',
      name: '',
      keepAlive: true,
      component: MenuList2,
      meta: {
        keepAlive: false
      }
    }, {
      path: '/MenuDetail',
      name: '',
      component: MenuDetail,
      hidden: true,

      meta: {
        keepAlive: false
      }
    }, {
      path: '/Voucher',
      name: '',
      component: Voucher,
      meta: {
        keepAlive: false
      }
    }]
  },

  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/MyCurOrder',
      name: '()',
      component: MyCurOrder
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/MyOrder',
      name: '',
      component: MyOrder
    }]
  },

  {
    path: '/home',
    component: Home,
    children: [{
      path: '/jiedan',
      name: '',
      component: ReceiveList
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/MenuMangeList',
      name: '',
      keepAlive: false,
      component: MenuMangeList
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/OrdersManage',
      name: '',
      component: OrdersManage
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/MoneyManage',
      name: '',
      component: MoneyManage
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/CateMana',
      name: '',
      component: CateMana
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/PostBlog',
      name: '',
      component: PostBlog
    }]
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/BlogList',
      name: '',
      keepAlive: true,
      component: BlogList
    }]
  }, {
    path: '/BlogDetail',
    name: 'BlogDetail',
    hidden: true,
    component: BlogDetail
  },
  {
    path: '/Home',
    component: Home,
    children: [{
      path: '/DateSign',
      name: '',
      keepAlive: true,
      component: DateSign
    }]
  },
  {

    path: '/Example',
    name: 'Example',
    keepAlive: true,
    hidden: true,
    keepAlive: true,
    component: Example

  },
 
  {
    path: '/Map',
    component: Map,
    children: [{
      path: '/Map',
      name: '',
      keepAlive: true,
      component: Map
    }]
  } 

  ]
})


