<template>
  <el-form
    class="login-container"
    label-position="left"
    label-width="0px"
    :model="loginForm"
    :rules="rules"
    ref="loginForm"
    v-loading="loading"
    style="margin-top:20px;margin-left:20px"
  >
    <h3 class="login_title"></h3>
    <el-form-item prop="email">
      <el-input
        type="text"
        v-model="loginForm.email"
        auto-complete="off"
        placeholder=""
      ></el-input>
    </el-form-item>
    <el-form-item prop="code">
      <span>
        <el-input
          type="text"
          v-model="loginForm.code"
          auto-complete="off"
          placeholder=""
          style="width: 130px"
        ></el-input>
      </span>
      &emsp;
      <span>
        <el-button :disabled="time > 0" @click="btnClick">
          {{ time > 0 ? `${time}` : "" }}
        </el-button>
      </span>
    </el-form-item>
    <el-dialog title="" :visible.sync="dialogVisible" :append-to-body="true">
      <p>1. ,,</p>
        <p>2. ,,</p>
        <p>3. ,5,</p>
        <p>4. ,1,</p>
    </el-dialog>
    <el-checkbox v-model="checked"></el-checkbox
    >&emsp;<span @click="dialogVisible = true" style="color:blue;text-decoration: underline;"></span>
    <Vcode :show="isShow" @success="success" @close="close" />
     
    <el-form-item style="width: 100%;margin-top:12px">
      <el-button
        type="primary"
        @click.native.prevent="submit"
        style="width: 100%"
        ></el-button
      >
    </el-form-item>
  </el-form>
</template>
  
  <script>
import { postRequest } from "../utils/api";
import Vcode from "vue-puzzle-vcode";
export default {
  components: {
    Vcode,
  },

  data() {
    return {
      dialogVisible: false,
      isShow: false,
      time: 0,
      why: "",
      checked: false,
      loginForm: {
        email: "",
        code: "",
      },
      rules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "string",
            message: "",
            trigger: "blur",
            transform(value) {
              if (
                !/^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/.test(
                  value
                )
              ) {
                return true;
              } else {
              }
            },
          },
          {
            type: "string",
            message: "30",
            trigger: "blur",
            max: 30,
          },
        ],
      },
      loading: false,
    };
  },
  mounted() {},

  methods: {
    btnClick() {
      this.$refs["loginForm"].validate((valid) => {
        if (valid) {
          this.sendCode();
          // 
          this.time = 120;
          // 
          const interval = setInterval(() => {
            // if
            this.time--;
            // 
            if (this.time < 0) {
              clearInterval(interval);
            }
          }, 1000);
        } else {
          return false;
        }
      });
    },
    submit() {
      if (!this.checked) {
        this.$message({ type: "error", message: "" });
        return false;
      }
      this.isShow = true;
    },
    success(msg) {
      this.submitClick();
      this.isShow = false; // 
    },
    // 
    close() {
      this.isShow = false;
    },
    sendCode() {
      this.$message({
        type: "success",
        message: "",
      });
      postRequest("/email/send", {
        email: this.loginForm.email,
        code: this.loginForm.code,
      }).then((resp) => {});
    },
    submitClick() {
      var _this = this;
      this.loading = true;
      this.$refs["loginForm"].validate((valid) => {
        if (valid) {
          postRequest("/email/login", {
            email: this.loginForm.email,
            code: this.loginForm.code,
          }).then(
            (resp) => {
              _this.loading = false;
              if (resp.status == 200) {
                //
                var json = resp.data;
                if (json.status == "success") {
                  _this.$router.replace({ path: "/home" });
                  sessionStorage.setItem("token", json.msg);
                } else {
                  _this.$alert(resp.data.msg, "!");
                }
              } else {
                //
                _this.$alert("!", "!");
              }
            },
            (resp) => {
              _this.loading = false;
              _this.$alert("!", "!");
            }
          );
        } else {
          this.loading = false;
          return false;
        }
      });
    },
  },
};
</script>
  <style>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.login_remember {
  margin: 0px 0px 35px 0px;
  text-align: left;
}
</style>
  


