<template>
  <div v-show="isAdmin">
    <!-- Form -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="menu">
        <el-form-item label="">
          <el-input v-model="menu.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="menu.price" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="menu.cost" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="menu.introduction" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="curtype" placeholder="">
            <el-option
              v-for="(item, index) in label"
              :key="index"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <br />
      <p style="text-align: left">()</p>
      <br />
      <el-upload
        action=""
        ref="upload"
        :on-change="handleChange"
        :on-remove="handleRemove"
        :auto-upload="false"
        list-type="picture-card"
        :limit="1"
      >
        <i class="el-icon-plus"></i>
      </el-upload>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addMenu()" :disabled="flag2 == 1"
          > </el-button
        >
      </div>
    </el-dialog>
    <!-- Form2 -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <br />
      <p style="text-align: left">()</p>
      <br />
      <el-upload
        ref="elupload"
        action=""
        :limit="5"
        :on-remove="handleRemove2"
        :on-change="isDisable"
        :auto-upload="false"
        :http-request="handleupload"
        list-type="picture-card"
      >
        <i class="el-icon-plus"></i>
      </el-upload>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button type="primary" @click="uploadImgMany()" :disabled="flag == 1"
          > </el-button
        >
      </div>
    </el-dialog>
    <el-container class="menu_list">
      <el-main class="main">
        <el-tabs @tab-click="handleClick" type="border-card">
          <el-tab-pane :label="lab" v-for="(lab, index) in label" :key="index">
            <menu_table
              :state="states[index]"
              ref="load"
              :category="lab"
            ></menu_table>
          </el-tab-pane>
        </el-tabs>
      </el-main>
      <el-footer>
        <el-button type="danger" size="mini" @click="dialogFormVisible = true"
          ></el-button
        >
        <el-button type="primary" size="mini" @click="dialogFormVisible2 = true"
          ></el-button
        >

        <br />
      </el-footer>
    </el-container>
  </div>
</template>
<script>
import MenuManage from "@/components/MenuManage";
import {
  deleteRequest,
  getRequest,
  postRequest,
  postRequest2,
} from "../utils/api";
import { uploadFileRequest } from "../utils/api";
export default {
  mounted: function () {
    var _this = this;
    getRequest("/isAdmin").then((resp) => {
      if (resp.status == 200) {
        _this.isAdmin = resp.data;
        if (_this.isAdmin == false)
          _this.$message({ type: "error", message: "" });
      }
    });
    getRequest("/admin/category/all").then((resp) => {
      var json = resp.data;
      for (var i = 0; i < json.length; i++) {
        this.label.push(json[i].name);
        this.states.push(json[i].id);
      }
    });
  },
  data() {
    return {
      states: [],
      label: [],
      fileList: [],
      carousels: {},
      centerDialogVisible: false,
      isAdmin: false,
      flag: 0,
      flag2: 0,
      curtype: "",
      //
      menu: {
        title: "",
        price: "",
        cost: "",
        introduction: "",
      },
      dialogFormVisible: false,
      dialogFormVisible2: false,
      imgurl: "",
      imgurls: [],
    };
  },
  methods: {
    handleChange(file, fileList) {
      var formdata = new FormData();
      var _this = this;
      var isLt10M = file.size / 1024 / 1024 < 10;
      var i = file.name.lastIndexOf(".");
      var s = file.name.substr(i + 1);
      if ("jpg,jpeg,png".indexOf(s) == -1) {
        _this.$message.error("");
        _this.flag2 = 1;
        return false;
      }
      if (!isLt10M) {
        _this.flag2 = 1;
        _this.$message.error("10MB!");
        return false;
      }

      fileList.map((item) => {
        //fileList
        formdata.append("file", item.raw);
        //formdata
      });
      uploadFileRequest("/upload/img", formdata).then((res) => {
        this.imgurl = res.data.msg;
      });
    },

    addMenu() {
      var _this = this;
      var i = 0;
      for (; i < this.label.length; i++) {
        if (this.label[i] == this.curtype) {
          break;
        }
      }
      postRequest2("/menus/addOne", {
        title: _this.menu.title,
        price: _this.menu.price,
        cost: _this.menu.cost,
        introduction: _this.menu.introduction,
        tid: _this.states[i],
        location: _this.imgurl,
      }).then(
        (resp) => {
          if (resp.status == 200) {
            var data = resp.data;
            _this.$message({ type: data.status, message: data.msg });
            if (data.status == "success") {
              this.$refs.upload.clearFiles();
              this.menu = [];
              this.imgurl = "";
              this.dialogFormVisible = false;
              this.$refs.load[i].loadMenus(1, 8);
            }
          } else {
            _this.$message({ type: data.status, message: data.msg });
          }
          _this.loading = false;
        },
        (resp) => {
          _this.loading = false;
          _this.$message({
            type: "error",
            message: ",!",
          });
        }
      );
    },

    //
    // handleClose0(tag) {
    //   this.recai.splice(this.recai.indexOf(tag), 1);
    //   this.$refs.recaitable.deleteRow(tag);
    // },
    //

    handleClick() {},
    // 
    handleRemove(file, fileList) {
      // 
      if (this.flag2 == 0)
        deleteRequest("/upload/delete", { imgUrl: this.imgurl });
      this.imgurl = "";
      this.flag2 = 0;
    },
    uploadImgMany() {
      let formdata = new FormData();
      this.$refs.elupload.submit(); // 
      if (this.fileList.length == 0) {
        this.$message({ type: "error", message: "" });
        return false;
      }
      this.$refs.elupload.clearFiles();
      this.fileList.forEach((item) => {
        formdata.append("file", item); //formdata
      });
      formdata.append("score", 4);
      uploadFileRequest("/upload/imgMany", formdata).then((resq) => {
        this.fileList = [];
        this.dialogFormVisible2 = false;

        deleteRequest("/img/deleteAll").then((res) => {
          postRequest("/img/saveAll", { imgUrls: resq.data }).then((res) => {
            this.$message({ type: "success", message: "" });
          });
        });
      });
      this.fileList = [];
    },
    handleupload(param) {
      this.fileList.push(param.file); // FormDataparam
    },

    isDisable(file) {
      var _this = this;
      if (this.flag == 1) {
        _this.$message.error("");
        return false;
      }
      var isLt10M = file.size / 1024 / 1024 < 10;
      var s = file.name.substr(file.name.lastIndexOf(".") + 1);
      if ("jpg,jpeg,gif,png".indexOf(s) == -1) {
        _this.$message.error("");
        _this.flag = 1;
        return false;
      }
      if (!isLt10M) {
        _this.flag = 1;
        _this.$message.error("10MB!");
        return false;
      }
    },
    handleRemove2() {
      this.$refs.elupload.submit();
      var can = true;
      this.fileList.forEach((file) => {
        var isLt10M = file.size / 1024 / 1024 < 10;
        if (["image/jpeg", "image/png"].indexOf(file.type) == -1) {
          can = false;
        }
        if (!isLt10M) {
          can = false;
        }
      });
      if (can) this.flag = 0;
      this.fileList = [];
    },
  },

  components: {
    menu_table: MenuManage,
  },
};
</script>
<style>
.menu_list > .header,
.footer {
  background-color: #ececec;
  margin-top: 10px;
  padding-left: 5px;
  display: flex;
  justify-content: flex-start;
}

.article_list > .main {
  /*justify-content: flex-start;*/
  display: flex;
  flex-direction: column;
  padding-left: 0px;
  background-color: #fff;
  padding-top: 0px;
  margin-top: 8px;
}
</style>



